.social-icons {
  padding: 0.25rem 0;
  font-size: 3.5rem; }
  .social-icons a {
    padding: 0 0.625rem; }
    .social-icons a:hover svg, .social-icons a:focus svg, .social-icons a:active svg {
      -webkit-box-shadow: 0 0 1px transparent;
              box-shadow: 0 0 1px transparent;
      color: #81cfb5;
      -webkit-transition: ease 0.5s;
      -o-transition: ease 0.5s;
      transition: ease 0.5s; }



